import React from "react";
import "./styles.css"

const aboutUs = props => (
    <div className="row jumbotron jumbotron-fluid" id="hero-bg">
        <div className="container">
            <h1 className="display-4">About Us</h1>
            <p className="lead">This page is under construction. Sorry for any inconvenience.</p>
            <hr className="my-4" />
            <p className="white-text">For now, you can login or sign up to begin.</p>
            <a className="btn btn-light" href="/" role="button">Go to Login Page</a>
        </div>
    </div>
)

export default aboutUs;


